@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap);
/* mediaquery */
@media screen and (min-width: 1200px) and (max-width: 1330px) {
  .about_title {
    font-size: 1.3rem;
  }

  .about_list {
    padding: 0px;
  }
  .about_item {
    padding-left: 0;
  }

  .skill_item {
    padding-right: 10px;
  }
}

@media screen and (min-width: 900px) and (max-width: 1200px) {
  .about_title {
    font-size: 1.1rem;
  }

  .about_profile img {
    width: 200px;
    border-radius: 50%;
  }

  .about_list {
    flex-wrap: wrap;
  }

  .info {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
  }

  .about-text {
    padding-left: 10px;
  }

  .second {
    margin-left: -90px;
  }

  .about_content {
    padding-left: 50px;
  }

  .skill_list {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin-left: -50px;
  }
}

@media screen and (max-width: 899px) {
  .about_profile img {
    width: 250px;
    border-radius: 50%;
  }

  .info {
    display: block;
  }

  .about_list {
    flex-wrap: wrap;
  }

  .skill_list {
    display: block;
    flex-wrap: wrap;
  }
}

.container {
  width: 100%;
  height: auto;
  padding-top: 50px;
  /* header & footer height */
  margin: -50px 0 -40px 0;
  text-align: center;
}

.about_title {
  padding-top: 5rem;
  font-size: 1.5rem;
  color: #3e3e3e;
  font-weight: 100;
}
.about-text {
  font-size: 1.4rem;
}

.about_profile img {
  width: 180px;
  border-radius: 50%;
}

.about_list {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: col;
  justify-content: center;
  padding: 10px;
}

.about_item {
  padding-left: 80px;
}

.about_content {
  width: 350px;
  text-align: left;
  padding-bottom: 20px;
}

.about_content p {
  font-size: 0.9rem;
  color: #3e3e3e;
  margin: 10px;
}

.skill_list {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
}

.skill_item {
  width: 350px;
  padding-right: 20px;
}

.skill_item li {
  display: flex;
  flex-direction: row;
  text-align: center;
}

.skill_item p {
  text-align: center;
  font-size: 0.8rem;
  color: #3e3e3e;
  padding: 10px 0 0 10px;
}

.Modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.16);
}

.Modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 620px;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
}
.Modal > * {
  margin-left: 16px;
  margin-right: 16px;
}
.Modal > *:first-child {
  margin-top: 16px;
}
.Modal h2.title {
  font-weight: 600;
  color: #000;
}
.Modal .content {
  border-top: 1px solid #bebebe;
  margin-top: 16px;
}

.Modal .content p {
  padding: 10px;
  font-size: 0.9rem;
  text-align: left;
  color: #3e3e3e;
}

.content-subtitle {
  font-weight: bold;
  color: #000;
  margin-right: 10px;
}

.gitlink {
  font-style: italic;
  font-weight: 500;
  text-decoration: none;
}
.gitlink:hover {
  cursor: pointer;
}
.Modal .button-wrap {
  margin: 0;
  margin-top: 8px;
}
.Modal .button-wrap button {
  width: 100%;
  padding: 12px 0;
  border-radius: 0 0 10px 10px;
  background-color: #3e3e3e;
  font-size: 0.9rem;
  color: #fff;
  border: 0;
  cursor: pointer;
}
.Modal .button-wrap button:hover {
  background-color: #bebebe;
  outline: none;
}
.Modal .button-wrap button:active {
  background-color: #bebebe;
  outline: none;
}

/* Layout */
html,
body {
  font-family: "Poppins", "Noto Sans KR" sans-serif;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

header,
footer {
  user-select: none;
  -webkit-user-select: none;
  -webkit-user-drag: none;
  -ms-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
}
body {
  width: 100%;
  min-height: 100%;
}

#root,
.App {
  width: 100%;
  height: 100%;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

/* About page */

.icon {
  margin-right: 10px;
}

/* Projects page */

.project__img {
  width: auto;
  height: 220px;
  border-radius: 10px;
  box-shadow: 8px 8px 10px rgba(0, 0, 0, 0.2);
  margin: 10px;
}

.project__img:hover {
  height: 230px;
  transition: 0.3s;
  cursor: pointer;
}

